import React, { useState } from "react";

const Card = () => {
  const [input, setInput] = useState("");
  const [items, setItems] = useState([]);
  const [editItems, setEditItems] = useState("");
  const [toggleButton, setToggleButton] = useState(false);

  // Add item in the list
  const addItem = () => {
    if (!input) {
      alert("Please enter an item first !!");
    } else if (toggleButton && input) {
      setItems(
        items.map((curElem) => {
          if (curElem.id === editItems) {
            return { ...curElem, name: input };
          } else {
            return curElem;
          }
        })
      );
      setInput([]);
      setEditItems(null);
      setToggleButton(false);
    } else {
      const newInputData = {
        id: new Date().getTime().toString(),
        name: input,
      };
      setItems([...items, newInputData]);
      setInput("");
    }
  };

  const addInput = (event) => {
    setInput(event.target.value);
  };

  // Delete item from list
  const deleteItem = (index) => {
    const updatedItems = items.filter((curElem) => {
      return curElem.id !== index;
    });
    setItems(updatedItems);
  };

  // Edit or Update item from the list
  const editItem = (index) => {
    const editedItem = items.find((curElem) => {
      return curElem.id === index;
    });
    setInput(editedItem.name);
    setEditItems(index);
    setToggleButton(true);
  };

  // Clear the list
  const clearAll = () => {
    setItems([]);
  };

  return (
    <>
      <div className="container">
        <div className="child-container">
          <figure>
            <img
              src="data:image/png;base64,"
              alt="logo"
            />
            <h2>TO-DO LIST</h2>
            <figcaption>Add your list here ðŸ™‚</figcaption>
          </figure>
          <div className="input-field">
            <input
              type="text"
              placeholder="âœ Add items..."
              className="input-items"
              onChange={addInput}
              value={input}
            />
            {toggleButton ? (
              <i className="fas fa-edit" onClick={addItem} />
            ) : (
              <i className="fa fa-plus" onClick={addItem} />
            )}
            <span className="border-bottom"></span>
          </div>
          <div className="add-items">
            {items.map((curElem) => {
              return (
                <div className="each-item" key={curElem.id}>
                  <h3>{curElem.name} </h3>
                  <div className="btn">
                    <i
                      className="fas fa-edit"
                      onClick={() => editItem(curElem.id)}
                    ></i>
                    <i
                      className="fas fa-trash-alt"
                      onClick={() => deleteItem(curElem.id)}
                    ></i>
                  </div>
                </div>
              );
            })}
          </div>
          <div className="clear-btn">
            <button className="btn2" onClick={clearAll}>
              CLEAR LIST
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Card;
